.language-container {
  text-align: center;
  margin-top: 20px;
}

.language-heading {
  text-align: center;
  margin-bottom: 20px; /* Add some space below the heading */
}

.language-box {
  padding: 10px;
  margin: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-align: center; /* Center text */
}

.language-box:hover {
  background-color: #f0f0f0;
}
