.favorite-shows-container {
    text-align: center;
    margin-top: 20px;
  }
  
  .favorite-shows-container textarea {
    width: 80%;
    height: 150px;
    margin: 20px auto;
  }
  
  .next-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .next-button:hover {
    background-color: #0056b3;
  }


