



.card-container {
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  
  .card {
    margin: 10px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    width: 200px;
    text-align: center;
  }
  
  .card:hover {
    background-color: #f0f0f0;
  }
  
  /* LanguageSelection.css */
  .language-list {
    list-style-type: none;
    padding: 0;
  }
  
  .language-list li {
    cursor: pointer;
    margin-bottom: 10px;
  }
  
  .language-list li:hover {
    color: blue;
  }
  
  /* ContentDetails.css */
  .content-details-container {
    text-align: center;
  }
  
  .content-details-container textarea {
    width: 80%;
    height: 150px;
    margin: 20px auto;
  }
  
  .content-details-container button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .content-details-container button:hover {
    background-color: #0056b3;
  }
  
  /* FavoriteShows.css */
  .favorite-shows-container {
    text-align: center;
  }
  
  .favorite-shows-container textarea {
    width: 80%;
    height: 150px;
    margin: 20px auto;
  }
  
  .favorite-shows-container button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .favorite-shows-container button:hover {
    background-color: #0056b3;
  }
  .card {
    border: 1px solid #3e2a8f;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .card hr {
    margin: 10px 0;
  }
  

.horizontal{
  color:#3e2a8f;
  height:5vh;
  margin-top:20px;
  background-color: antiquewhite;
}

.soda{
  font-family:monospace;
  font-size: 1.8em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  margin: auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


