.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .google-login-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #DB4437; /* Google Red */
    color: white;
    cursor: pointer;
  }
  
  .google-icon {
    width: 35px; /* Adjust as needed */
    height: 30px; /* Adjust as needed */
    margin-right: 10px;
  }
  