.content-details-container {
  text-align: center;
  margin-top: 20px;
}

.genre-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.genre-card {
  padding: 10px 20px;
  margin: 10px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.genre-card:hover {
  background-color: #e0e0e0;
}
